	import uniList from "@/components/uni-list/uni-list.vue"
	import uniListItem from "@/components/uni-list-item/uni-list-item.vue"
	import {
			mapState,
	        mapMutations  
	    } from 'vuex';
	import md5 from '../../kayida/common/md5.js'
	import indexBackgroundImage from "@/static/user/bg.png"
	import uniPopup from "@/components/uni-popup/uni-popup.vue"

    export default {
		components:{uniList,uniListItem,uniPopup},
		computed: {  
		            ...mapState(['forcedLogin','userInfo'])  
		        },
		data() {
			return{
				indexBackgroundImage:indexBackgroundImage,
				bind:'',
				user_list:[
					{img:"zhmx",text:"账户明细"},
					{img:"tglj",text:"新闻资讯"},
					{img:"ptsc",text:"平台手册"},
					{img:"wdxx",text:"我的消息"},
					{img:"wltg",text:"物料推广"},
					{img:"tgfy",text:"推广费用"},
					{img:"kczx",text:"课程中心"},
					{img:"kyjj",text:"卡易讲解"},
				],
				user_message: '',
				level_descr:'',
				team_money:'',
				e_mondy:'',
				m:false,
				guide:0,
				lists:'',
				max:'',
			}
		},
		onPullDownRefresh() {
			if(this.forcedLogin){
				uni.reLaunch({
					url:"../login/login"
				})
			}
			let data = {
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/user/index',data,'GET').then(res =>{
					this.user_message = res.data.retval
					this.bind = res.data.retval.is_bind
					if(res.data.code === 1000) {
						uni.stopPullDownRefresh();
					}else if(res.data.code === 1400) {
							uni.showToast({
								title:"登录失效，请重新登录",
								icon:"none"
							})
							uni.reLaunch({
								url:"../login/login"
							})
					}
				}
			)
			this.request.httpRequest('api/user/reward',data,'GET').then(res =>{
					this.team_money = JSON.stringify(res.data.retval.reward2.team_money)
					this.e_mondy = JSON.stringify(res.data.retval.reward2.e_mondy)
				}
			)
		},
        methods: {
			openGuide() {
				this.guide = 1
			},
			nGuide() {
				this.guide = 0
			},
			open(){
				this.$refs.popup.open()
				this.level_descr = this.user_message.level_descr
			},
			close(){
				this.$refs.popup.close()
			},
			toInformation() {
				uni.navigateTo({
					url:"../userinformation/userinformation"
				})
			},
            toWithDrawal() {
				uni.navigateTo({
					url:"../withdrawal/withdrawal"
				})
			},
			toTeamAward() {
				// uni.navigateTo({
				// 	url:"../teamaward/teamaward"
				// })
				uni.showToast({
					title:"暂未开放",
					icon:"none"
				})
			},
			logOut() {
				this.logout()
				this.outbind()
				uni.reLaunch({
					url:"../login/login"
				})
			},
			toMyTeam() {
				uni.navigateTo({
					url:"../myteam/myteam"
				})
			},
			toService() {
				uni.navigateTo({
					url: "../service/service"
				})
			},
			toAdvice(){
				uni.navigateTo({
					url: "../advice/advice"
				})
			},
			toProblem() {
				uni.navigateTo({
					url:"../problem/problem"
				})
			},
			userFunc(t){
				if(t === 'wdxx'){
					uni.navigateTo({
						url:"../mynews/mynews"
					})
				}else if (t === 'zhmx'){
					uni.navigateTo({
						url:"../accountdetails/accountdetails"
					})
				}else if(t === 'ptsc'){
					uni.navigateTo({
						url:"../handbook/handbook"
					})
				}else if(t === 'kczx'){
					uni.navigateTo({
						url:"../coursecenter/coursecenter"
					})
				}else if(t === 'tglj'){
					uni.navigateTo({
						url:"../promotelinks/promotelinks"
					})
				}else if(t === 'wltg'){
					uni.navigateTo({
						url:"../materials/materials"
					})
				}else if(t === 'tgfy'){
					uni.navigateTo({
						url:"../commission/commission"
					})
				}else if(t === 'kyjj'){
					uni.showToast({
						title:"暂未开放",
						icon:"none"
					})
				}
			},
			...mapMutations(['logout','whetherbind','outbind']),
        },
		onLoad() {
			if(this.forcedLogin){
				uni.reLaunch({
					url:"../login/login"
				})
			}
			let data = {
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/user/index',data,'GET').then(res =>{
					if(res.data.code === 1400) {
					}
					this.user_message = res.data.retval
					this.bind = res.data.retval.is_bind
				}
			)
			this.request.httpRequest('api/user/reward',data,'GET').then(res => {
				this.lists = res.data.retval.reward2.lists
				this.max = res.data.retval.reward2.max
				this.team_money = JSON.stringify(res.data.retval.reward2.team_money)
				this.e_mondy = JSON.stringify(res.data.retval.reward2.e_mondy)
			})
			uni.startPullDownRefresh();
		},
		onShow() {
			if(this.forcedLogin){
				uni.reLaunch({
					url:"../login/login"
				})
			}
			let data = {
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/user/index',data,'GET').then(res =>{
					this.user_message = res.data.retval
					this.bind = res.data.retval.is_bind
					if(res.data.code === 1000) {
						uni.stopPullDownRefresh();
					}else if(res.data.code === 1400) {
							uni.showToast({
								title:"登录失效，请重新登录",
								icon:"none"
							})
							uni.reLaunch({
								url:"../login/login"
							})
					}
				}
			)
			this.request.httpRequest('api/user/reward',data,'GET').then(res =>{
					this.team_money = JSON.stringify(res.data.retval.reward2.team_money)
					this.e_mondy = JSON.stringify(res.data.retval.reward2.e_mondy)
				}
			)
		}
    }
